<template>
  <div class="table1">
    <div class="add">
      <button @click="dialogFormVisible = true">新增</button>
    </div>
    <table cellpadding="0" cellspacing="0">
      <thead>
        <tr>
          <th>序号</th>
          <th>课题名称</th>
          <th>时长</th>
          <th>需要掌握的内容</th>
          <th>学习目标</th>
          <th>参考教材</th>
          <th>操作</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item,index) in newsList" :key="index">
          <td width="7%">{{index+1}}</td>
          <td width="12%">{{item.knowName}}</td>
          <td width="7%">{{item.knowTime}}</td>
          <td width="18%">{{item.knowRemark}}</td>
          <td width="12%">{{item.knowDest}}</td>
          <td width="15%">
            <a v-for="(cklas,i) in item.cklas" :key="i">
                  {{cklas.attachName}},
                 </a>
          </td>
          <td width="15%">
            <el-button type="text" class="delete" @click="deletelist(newsList,index)">删除</el-button>
            <el-button type="text" class="edit" @click="edit(item)">编辑</el-button>
          </td>
        </tr>
      </tbody>
    </table>
    <el-dialog title="编辑" :visible.sync="dialogVisible" width="60%" size="mini" class="main-content">
      <el-form :label-position="labelPosition" :inline="true" label-width="130px" :rules="rules" :model="editDetail" size="mini" ref="formName">
        <el-form-item label="课题名称" prop="knowName">
          <el-input v-model="editDetail.knowName" placeholder="课题名称" name="knowName"></el-input>
        </el-form-item>
        <el-form-item label="时长" prop="knowTime">
          <el-input v-model.number="editDetail.knowTime" placeholder="时长" name="knowTime"></el-input>
        </el-form-item>
        <el-form-item label="需要掌握的内容" prop="knowRemark">
          <el-input v-model="editDetail.knowRemark" placeholder="需要掌握的内容" name="knowRemark"></el-input>
        </el-form-item>
        <el-form-item label="学习目标" prop="knowDest">
          <el-input v-model="editDetail.knowDest" placeholder="学习目标" name="knowDest"></el-input>
        </el-form-item>
         <el-form-item label="技能类型" prop="knowType">
          <el-select v-model="editDetail.knowType" placeholder="请选择技能类型">
            <el-option v-for="item in knowType" :key="item.typeId" :label="item.typeValue" :value="item.typeId"></el-option>
          </el-select>
        </el-form-item>
        <el-upload class="upload-demo clearfix" ref="upload" action="www.baidu.com" :on-remove="handleRemove" :file-list="fileList" :auto-upload="false">
          <div class="updateweekly">上传周报附件</div>
          <el-button slot="trigger" size="small" type="primary" class="selfile">选取文件</el-button>
          <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload(editDetail, filesList)">上传到服务器</el-button>
        </el-upload>
      </el-form>
      <span slot="footer" class="dialog-footer">
            <el-button @click="update(editDetail,'formName',filesList)" size="small">更新</el-button>
        <el-button type="primary" @click="dialogVisible = false" size="small">取消</el-button>
           </span>
    </el-dialog>
    <el-dialog title="新增" :visible.sync="dialogFormVisible" width="60%" size="mini" class="main-content">
      <el-form :label-position="labelPosition" :inline="true" label-width="130px" :rules="rules" :model="addDetail" size="mini" ref="formName">
        <el-form-item label="课题名称" prop="knowName">
          <el-input v-model="addDetail.knowName" placeholder="课题名称" name="knowName"></el-input>
        </el-form-item>
        <el-form-item label="时长" prop="knowTime" >
          <el-input v-model.number="addDetail.knowTime" placeholder="时长" name="knowTime"></el-input>
        </el-form-item>
        <el-form-item label="需要掌握的内容" prop="knowRemark">
          <el-input v-model="addDetail.knowRemark" placeholder="需要掌握的内容" name="knowRemark"></el-input>
        </el-form-item>
        <el-form-item label="学习目标" prop="knowDest">
          <el-input v-model="addDetail.knowDest" placeholder="学习目标" name="knowDest"></el-input>
        </el-form-item>
        <el-form-item label="技能类型" prop="knowType">
          <el-select v-model="addDetail.knowType" placeholder="请选择技能类型">
            <el-option v-for="item in knowType" :key="item.typeId" :label="item.typeValue" :value="item.typeId"></el-option>
          </el-select>
        </el-form-item>
        <el-upload class="upload-demo clearfix" ref="upload" action="www.baidu.com" :on-remove="handleRemove" :file-list="fileList" :auto-upload="false">
          <div class="updateweekly">上传周报附件</div>
          <el-button slot="trigger" size="small" type="primary" class="selfile">选取文件</el-button>
          <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
        </el-upload>
      </el-form>
      <span slot="footer" class="dialog-footer">
            <el-button @click="adddetail(addDetail,'formName',fileList)" size="small">新增</el-button>
            <el-button type="primary" @click="dialogFormVisible= false" size="small">取消</el-button>
          </span>
    </el-dialog>
    <div class="hello">
      <el-row style="margin-top: 30px" type="flex" justify="end">
        <el-col :span="18">
          <el-pagination style="float: right" @size-change="pageSizeChange" @current-change="currentPageChange" :current-page="currentPage" :page-sizes="[5, 10]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total">
          </el-pagination>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import service from "../scripts/service.js";
export default {
  name: "table1",
  data() {
    return {
      addDetail: {
        knowGroupType: String(2),
        knowType: ""
      },
      editDetail: {},
      filesList: [],
      knowtype: [],
      index: 0,
      dialogVisible: false,
      dialogFormVisible: false,
      newsList: {},
      fileList: [
        {
          attachName: "",
          attachRemark: ""
        },
        {
          attachName: "",
          attachRemark: ""
        }
      ],
      editid: "",
      //搜索表单
      searchForm: {
        id: "",
        name: "",
        state: ""
      },
      //多选值
      multipleSelection: [],
      //当前页
      currentPage: 1,
      //分页大小
      pageSize: 10,
      //总记录数
      total: "",
      currentPageIndex: 0,
      rules: {
        knowName: [
          {
            required: true,
            message: "请输入课题名称",
            trigger: "blur"
          }
        ],
        knowTime: [
          {
            required: true,
            message: "请输入时长",
            trigger: "blur"
          },
          { type: "number", message: "时长必须为数字值" }
        ],
        knowRemark: [
          {
            required: true,
            message: "请输入需要掌握的内容",
            trigger: "blur"
          }
        ],
        knowDest: [
          {
            required: true,
            message: "请输入学习目标",
            trigger: "blur"
          }
        ],
        knowType: [
          {
            required: true,
            message: "请选择技能类型",
            trigger: "blur"
          }
        ]
      }
    };
  },
  mounted: function() {
    this.getList();
    this.getselect();
  },
  methods: {
    getselect() {
      service
        .getListType()
        .then(res => {
          this.knowType = res.data;
          console.log(this.knowType);
        })
        .catch(err => {
          console.log(err);
        });
    },
    getList() {
      let params = {
        pages: this.currentPage,
        pageSize: this.pageSize,
        knowGroupType: 2
      };
      console.log(params);
      service
        .getplanInfo(params)
        .then(res => {
          console.log(res);
          // 将获取回来的数据赋值给list
          this.newsList = res.data.list;
          this.total = res.data.total;
        })
        .catch(err => {
          console.log(err);
        });
    },
    //新增
    adddetail(formName, fileList) {
      this.$refs["formName"].validate(valid => {
        if (valid) {
          let params = {
            ckl: this.addDetail,
            cklas: this.fileList
          };
          console.log(params);
          service
            .addplanInfo(params)
            .then(response => {
              this.getList();
            })
            .catch(error => {
              console.log(error);
            });
          this.dialogFormVisible = false;
        } else {
          return false;
        }
      });
    },
    //删除
    deletelist(newsList, index) {
      this.$confirm("确认关闭？")
        .then(_ => {
          let params = {
            knowId: this.newsList[index].knowId
          };
          console.log(params);
          service
            .delplanInfo(params)
            .then(response => {
              console.log(response);
              // 加载列表
              this.getList();
            })
            .catch(error => {
              console.log(error);
            });
        })
        .catch(_ => {});
    },
    //编辑
    edit(item) {
      this.editDetail = {
        knowId: item.knowId,
        knowName: item.knowName,
        knowTime: item.knowTime,
        knowRemark: item.knowRemark,
        knowDest: item.knowDest,
        knowType: item.knowType,
        knowGroupType: String(2)
      };
      this.filesList = item.cklas;
      console.log(this.filesList);

      this.dialogVisible = true;
      this.editid = item.knowId;
    },
    //确认更新
    update(knowId, formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let params = {
            ckl: this.editDetail
          };
          console.log(params);
          service
            .updplanInfo(params)
            .then(response => {
              this.getList();
            })
            .catch(error => {
              console.log(error);
            });
          this.dialogVisible = false;
          alert("submit!");
        } else {
          return false;
        }
      });
    },
    //分页大小修改事件
    pageSizeChange: function(val) {
      console.log("每页 " + val + " 条");
      this.pageSize = val;
      this.getList();
    },
    //当前页修改事件
    currentPageChange: function(val) {
      this.currentPage = val;
      console.log("当前页: " + val);
      this.getList();
    },
    submitUpload(editDetail, filesList) {
      this.$refs.upload.submit();
      let params = {
        knowId: this.editDetail.knowId,
        ckla: this.filesList
      };
      console.log(params);
      service
        .addAttachInfo(params)
        .then(response => {
          this.getList();
        })
        .catch(error => {
          console.log(error);
        });
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    }
  }
};
</script>

<style scoped>
table {
  width: 100%;
  font-size: 14px;
  border: 1px solid #eee;
}
table thead th {
  background: #e5eff8;
  padding: 22px 18px 22px 18px;
  text-align: center;
}
table tbody td {
  padding: 10px 0px 10px 18px;
  text-align: left;
  border-bottom: 1px solid #eee;
  border-right: 1px solid #eee;
}
table span {
  margin: 0 10px;
  cursor: pointer;
}
.delete {
  color: red;
  width: 50px;
}
.edit {
  color: #008cd5;
  width: 50px;
}
.add {
  border: 1px solid #eee;
  margin-bottom: 10px;
  padding: 15px;
}
tbody tr:hover {
  background-color: #e5f0f86c;
}
.add button {
  background: #008cd5;
  border: 0;
  cursor: pointer;
  padding: 4px 15px;
  border-radius: 3px;
  color: #fff;
}
.hello {
  margin-top: -10px;
  margin-bottom: 10px;
}
.upload-demo {
  margin-left: 36px;
}
.dialog-footer {
  margin-top: 20px !important;
  margin-right: 40% !important;
  margin-bottom: 20px !important;
}
.updateweekly {
  margin-top: 5px;
  float: left;
}
.selfile {
  margin-left: 10px !important;
}
</style>


